import React, {useEffect, useState} from 'react';
import {useDispatch, useSelector} from "react-redux";
import {fetchPostData} from "../../../redux/thunks/fetchPostData";
// Styles
import 'grapesjs/dist/css/grapes.min.css';
import "../../../scss/gjs.scss";
// GrapesJS
import grapesjs from 'grapesjs';
import {deviceManager} from "./gjs_utils/settings/deviceManager";
import {blockManager} from "./gjs_utils/settings/blockManager";
import {layerManager} from "./gjs_utils/settings/layerManager";
import {styleManager} from "./gjs_utils/settings/styleManager";
import {traitManager} from "./gjs_utils/settings/traitManager";
import {selectorManager} from "./gjs_utils/settings/selectorManager";
import {panels} from "./gjs_utils/settings/panels";
import {plugins} from "./gjs_utils/settings/plugins";
import {styles} from "./gjs_utils/settings/styles";
import {storageManager} from "./gjs_utils/settings/storageManager";
import {addCommands} from "./gjs_utils/run/addCommands";
import {addGoogleFonts} from "./gjs_utils/run/addGoogleFonts";
import {addPanels} from "./gjs_utils/run/addPanels";
import {addBlocks} from "./gjs_utils/run/addBlocks";
import {addStyles} from "./gjs_utils/run/addStyles";
import {addTraits} from "./gjs_utils/run/addTraits";
import Canvas from "./gjs_utils/components/Canvas";
import Sidebar from "./gjs_utils/components/Sidebar";
import Top from "./gjs_utils/components/Top";
import Spinner from "../Loader/Spinner";
import {filterByLabel} from "../../../utils/objects";
import useTranslation from "../../../hooks/useTranslation";

const VisualBuilder = ({belongsTo, find, metaFieldId, postList, taxonomyList, template, showPreviewLink, exitLink}) => {

    // Visual Builder cannot be used under 960px resolution
    window.addEventListener("resize", function () {
        setBrowserWidth(document.body.clientWidth);
    });

    // manage global state
    const dispatch = useDispatch();
    const {loading: settingsLoading, fetched: settings} = useSelector(state => state.fetchSettingsReducer);

    // manage local state
    const [browserWidth, setBrowserWidth] = useState(document.body.clientWidth);
    const [editor, setEditor] = useState(null);
    const [blocks, setBlocks] = useState(null);
    const [loadingBlocks, setLoadingBlocks] = useState(false);
    const [leadingPostId, setLeadingPostId] = useState((postList.length > 0) ? postList[0].id : null);
    const [leadingTermId, setLeadingTermId] = useState((taxonomyList.length > 0) ? taxonomyList[0].term_id : null);
    const enableVisualEditor = (settings.length > 0 && filterByLabel(settings, 'key', 'enable_visual_editor') !== '') ? filterByLabel(settings, 'key', 'enable_visual_editor').value : false;

    // Fetching blocks
    useEffect(() => {

        dispatch(fetchPostData(leadingPostId));

        const fetchBlocks = async () => {
            const globals = document.globals;
            const blocksApiUrl = `${globals.site_url}${globals.rest_route_url}/template/block/${belongsTo}/${template}${find ? '/'+find : ''}${metaFieldId ? '/'+metaFieldId : ''}`;

            setLoadingBlocks(true);
            let response = await fetch(blocksApiUrl);
            response = await response.json();
            setLoadingBlocks(false);
            setBlocks(response);
        };

        fetchBlocks();
    }, []);

    // init GrapesJS
    useEffect(()=>{

        if(blocks !== null){

            // Disable body scroll
            document.body.style.overflow = 'hidden';

            // Init editor
            const editor = grapesjs.init({
                container: '.gjs-editor',
                fromElement: true,
                canvas: styles(),
                plugins: plugins(),
                blockManager: blockManager(),
                deviceManager: deviceManager(),
                layerManager: layerManager(),
                styleManager: styleManager(),
                selectorManager: selectorManager(),
                traitManager: traitManager(),
                storageManager: storageManager(belongsTo, template, find, metaFieldId),
                panels: panels(),
            });

            // Blocks
            addBlocks(editor, blocks);

            // Commands
            addCommands(editor, exitLink);

            // Panels
            addPanels(editor);

            // StyleManager
            addStyles(editor);

            // Traits
            addTraits(editor);

            // Google fonts
            addGoogleFonts(editor);

            setEditor(editor);
        }
    }, [blocks]);

    // Dynamically fetch post data on leading post ID changes
    useEffect(() => {
        dispatch(fetchPostData(leadingPostId));
    },[leadingPostId]);

    // Query blocks
    const blockQueryHandle = (query) => {
        const bm = editor.Blocks;
        const all = bm.getAll();
        let filter;
        query && (filter = all.filter(block => {
            return block.get('label').toLowerCase().indexOf(query.toLowerCase()) > -1;
        })) ||
        (filter = all.filter(block => true));
        bm.render(filter);
    };

    if(loadingBlocks && settingsLoading){
        return <Spinner/>;
    }

    if(enableVisualEditor !== "1"){
        return (
            <React.Fragment>
                <div style={{marginLeft: "22px"}} className="update-nag notice notice-warning">
                    {useTranslation("ALERT: The visual builder is disabled. Go to Settings page and enable it.")}
                </div>
            </React.Fragment>
        );
    }

    return  (
        <React.Fragment>
            <div style={{marginLeft: "22px"}} className={`update-nag notice notice-warning ${browserWidth >= 960 ? 'hidden' : ''}`}>
                {useTranslation("ALERT: The visual builder can be used with a minimum resolution of 960 px wide")}
            </div>
            <div className={`gjs-wrapper ${browserWidth < 960 ? 'hidden' : ''}`}>
                <Top/>
                <div className="gjs-main">
                    <Sidebar
                        blockQueryHandle={blockQueryHandle}
                    />
                    <Canvas
                        template={template}
                        belongsTo={belongsTo}
                        find={find}
                        leadingPostId={leadingPostId}
                        setLeadingPostId={setLeadingPostId}
                        postList={postList}
                        leadingTermId={leadingTermId}
                        setLeadingTermId={setLeadingTermId}
                        taxonomyList={taxonomyList}
                        showPreviewLink={showPreviewLink}
                    />
                </div>
            </div>
        </React.Fragment>
    );
};

export default VisualBuilder;