import React, {useEffect, useRef, useState} from 'react';
import {useParams} from "react-router-dom";
import {useSearchParams} from "../../../hooks/useSearchParams";
import {useDispatch, useSelector} from "react-redux";
import {metaTitle} from "../../../utils/misc";
import {fetchPosts} from "../../../redux/thunks/fetchPosts";
import Spinner from "../../reusable/Loader/Spinner";
import {metaTypes} from "../../../constants/metaTypes";
import {fetchTerms} from "../../../redux/thunks/fetchTerms";
import VisualBuilder from "../../reusable/VisualBuilder";

const Template = () => {

    // manage local state
    const {belongsTo, template, find, metaFieldId} = useParams();
    const queryString = useSearchParams();

    // manage global state
    const dispatch = useDispatch();
    const {fetched: fetchedPosts, loading: loadingPosts} = useSelector(state => state.fetchPostsReducer);
    const {fetched: fetchedTerms, loading: loadingTerms} = useSelector(state => state.fetchTermsReducer);

    // manage local state
    const didMountRef = useRef(false);
    const [fetchedSuccess, setFetchedSuccess] = useState(null);
    const showPreviewLink = (!(template === 'related' || template === 'meta_field'));

    const exitLink = () => {

        if(queryString.refer){
            return "/admin.php?page=advanced-custom-post-type#/"+queryString.refer;
        }

        if(belongsTo === metaTypes.TAXONOMY){
            return '/admin.php?page=advanced-custom-post-type#/taxonomies';
        }

        return "/admin.php?page=advanced-custom-post-type";
    };

    // meta title
    useEffect(() => {
        metaTitle(`${template}: manage template`);

        if(belongsTo === metaTypes.CUSTOM_POST_TYPE){
            dispatch(fetchPosts(find));
        } else if(belongsTo === metaTypes.TAXONOMY){
            dispatch(fetchTerms(find));
        } else {
            setFetchedSuccess(true);
        }

    }, []);

    // handle fetch outcome
    useEffect(() => {
        if (didMountRef.current){
            if(!loadingTerms && !loadingPosts){
                setFetchedSuccess(true);
            }
        } else {
            didMountRef.current = true;
        }
    }, [loadingPosts, loadingTerms]);

    if(!fetchedSuccess){
        return <Spinner/>;
    }

    return (
        <VisualBuilder
            belongsTo={belongsTo}
            template={template}
            find={find}
            metaFieldId={metaFieldId}
            postList={fetchedPosts}
            taxonomyList={fetchedTerms}
            showPreviewLink={showPreviewLink}
            exitLink={exitLink()}
        />
    );
};

export default Template;