
export const storageManager = (belongsTo, template, find, metaFieldId) => {

    const globals = document.globals;
    const urlStore = `${globals.site_url}${globals.rest_route_url}/template/store`;
    const urlLoad = `${globals.site_url}${globals.rest_route_url}/template/load/${belongsTo}/${template}${find ? '/'+find  : ''}${metaFieldId ? '/'+metaFieldId  : ''}`;

    return {
        autosave: false,
        type: 'remote',
        stepsBeforeSave: 3,
        options: {
            remote: {
                urlLoad: urlLoad,
                urlStore: urlStore,
                contentTypeJson: true,
                onStore: (data, editor) => {

                    const pagesHtml = editor.Pages.getAll().map(page => {
                        const component = page.getMainComponent();
                        return {
                            html: editor.getHtml({ component }),
                            css: editor.getCss({ component })
                        }
                    });

                    return { data, pagesHtml, template, belongsTo, find, metaFieldId };
                },
                onLoad: result => {
                    return result.data;
                },
            }
        }
    };
};