import {wpAjaxRequest} from "../../utils/ajax";
import {
    fetchPreviewLinkFailure,
    fetchPreviewLinkInProgress,
    fetchPreviewLinkSuccess
} from "../actions/fetchPreviewLinkActions";
import {isEmpty} from "../../utils/objects";

export const fetchPreviewLink = (belongsTo, find, template, id) => async (dispatch, getState) => {
    try {
        dispatch(fetchPreviewLinkInProgress());
        const fetched = await wpAjaxRequest('fetchPreviewLinkAction', {id: id, belongsTo: belongsTo, find:find, template:template});
        dispatch(fetchPreviewLinkSuccess(fetched));

        if(!isEmpty(fetched) && fetched.success === true){
            const previewLink = (template === 'archive') ? fetched.data.archive_link : fetched.data.single_link;
            window.open(previewLink, '_blank');
        }

    } catch ( e ) {
        dispatch(fetchPreviewLinkFailure(e));
    }
};