import {wpAjaxRequest} from "../../utils/ajax";
import {fetchTermsFailure, fetchTermsInProgress, fetchTermsSuccess} from "../actions/fetchTermsActions";

export const fetchTerms = (taxonomy, perPage, sortBy, sortOrder) => async (dispatch, getState) => {
    try {
        dispatch(fetchTermsInProgress());
        const fetched = await wpAjaxRequest('fetchTermsAction', {taxonomy:taxonomy, perPage:perPage, sortBy:sortBy, sortOrder:sortOrder});
        dispatch(fetchTermsSuccess(fetched));
    } catch ( e ) {
        dispatch(fetchTermsFailure(e));
    }
};