import {fetchPreviewLink} from "../../../../../redux/thunks/fetchPreviewLink";
import React from "react";
import {useDispatch} from "react-redux";
import {metaTypes} from "../../../../../constants/metaTypes";
import useTranslation from "../../../../../hooks/useTranslation";

const Canvas = ({ template, belongsTo, find, postList, showPreviewLink, setLeadingPostId, leadingPostId, leadingTermId, setLeadingTermId, taxonomyList}) => {

    const dispatch = useDispatch();

    return (
        <div className="editor-canvas">
            {showPreviewLink && postList.length > 0 && belongsTo === metaTypes.CUSTOM_POST_TYPE && (
                <div className="gjs-meta">
                    <div className="gjs-meta-element">
                        <span>Leading post:</span>
                        <select
                            onChange={e => {
                                setLeadingPostId(parseInt(e.target.value))
                            }}
                        >
                            {postList.map((post, index) => {
                                return <option key={index} value={post.id}>{post.title}</option>
                            })}
                        </select>
                        <button
                            onClick={e => {
                                e.preventDefault();
                                dispatch(fetchPreviewLink(belongsTo, find, template, leadingPostId) );
                            }}
                            className="primary"
                        >
                            {useTranslation("Preview")}
                        </button>
                    </div>
                </div>
            )}
            {showPreviewLink && taxonomyList.length > 0 && belongsTo === metaTypes.TAXONOMY && (
                <div className="gjs-meta">
                    <div className="gjs-meta-element">
                        <span>
                            {useTranslation("Leading term:")}
                        </span>
                        <select
                            onChange={e => {
                                setLeadingTermId(parseInt(e.target.value))
                            }}
                        >
                            {taxonomyList.map((taxonomy, index) => {
                                return <option key={index} value={taxonomy.term_id}>{taxonomy.name}</option>
                            })}
                        </select>
                        <button
                            onClick={e => {
                                e.preventDefault();
                                dispatch(fetchPreviewLink(belongsTo, find, template, leadingTermId) );
                            }}
                            className="primary"
                        >
                            {useTranslation("Preview")}
                        </button>
                    </div>
                </div>
            )}

            <div className="gjs-editor">
                <p>
                    {useTranslation("No content yet. Drag components in this canvas and start building something awesome!")}
                </p>
            </div>
        </div>
    );
};

export default Canvas;